
#preview {
  background: white;
  -webkit-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);
          box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);
  padding: 2em;
  color: #333;
  line-height: 1.2;
  overflow: auto;
}
#preview .button {
    margin: 15px 10px;
    border: 1px solid #2BC017;
    background: transparent;
    border-radius: 5px;
    color: #2BC017;
    cursor: pointer;
}
#preview .primary {
    background: #02af5f;
    color: white;
}
#preview * {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-variant: normal;
    font-weight: normal;
}
#preview ol {
    list-style: none;
}
#preview section + section {
    margin-top: 2em;
}
#preview p {
    white-space: pre-line;
}
#preview section > h2:first-child {
    display: inline-block;
    padding: .2em;
    margin-bottom: .5em;
}
#preview section[data-name="profile"] > h1 {
    margin: .1em 0;
    font-size: 4em;
}
#preview section[data-name="workHistory"] li + li,
  #preview section[data-name="projects"] li + li,
  #preview section[data-name="awards"] li + li {
    margin-top: 1em;
}
#preview section[data-name="workHistory"] li h3,
  #preview section[data-name="projects"] li h3,
  #preview section[data-name="awards"] li h3 {
    border-bottom: 1px solid #999;
    padding-bottom: .3em;
    margin-bottom: .3em;
}
#preview section[data-name="education"] li {
    line-height: 1.5;
}
#preview section[data-name="contacts"] td:first-child {
    padding-right: 1em;
}



